.container {
    display: flex;
    justify-content: space-between;
    align-self: baseline;
    margin: 40px 0;
    margin-left: 7.5%;
    margin-right: 7.5%;
    width: 85%;

    img {
        height: 50px;
        width: 180px;

        @media screen and (min-width: 425px){
            height: 50px;
            width: 150px;
        }
    }
  }
  
