.footer-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: black;
    color: white;
    justify-content: space-between;
    padding-top: 60px;
    width: 100%;
    height: 100px;
    
    img {
        width: 100px;
    }
    
    p {
        height: 20px;
        margin-bottom: 25px;
        color: white;
    }
        
    span {
        font-size: 1.2rem;
    }
}
