body {
    margin: 0;
    font-family: 'Montserrat';
}

.aboutPage {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.aboutBanner {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 223px;
    margin: 50px 0;

    @media screen and (max-width: 600px) {
        width: 90%;
        height: 120px;
        margin: 0 auto;
    }
}

.aboutBannerImg {
    border-radius: 20px;
    filter: brightness(0.7);

    @media screen and (max-width: 600px) {
        width: 100%;
    }
}